
































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import { Display } from "../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

import {
  ISolicitacoesNaoIntegradasView,
  ISolicitacoesNaoIntegradasController
} from "../../contracts/Solicitacao/SolicitacoesNaoIntegradasContract";

import { SolicitacoesNaoIntegradasController } from "../../controllers/Solicitacao/SolicitacoesNaoIntegradasController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class SolicitacoesNaoIntegradas extends Vue implements ISolicitacoesNaoIntegradasView {
  _controller?: ISolicitacoesNaoIntegradasController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string): void {
    this.textError = msg;
    this.dialogMessage = true;
  }
  showItens(list: any): void {
    this.itens = list;
  }
  fecharDialogMessage() {
    this.dialogMessage = false;
  }

  isMobile = false;

  headers = new Headers([
    new Header("Solicitante", "Solicitante", false, false),
    new Header("Centro Custo", "CentroCusto", false, false),
    new Header("Descrição", "Descricao", false, false),
    new Header("Data Solicitante", "DataSolicitacao", false, false),
    new Header("Data Vencimento", "DataVencimento", false, false)
  ]);

  itens = [];

  importLista() {
    this._controller!.importItens();
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new SolicitacoesNaoIntegradasController(this);

    this._controller.carregarItens();
  }
}
