import { ISolicitacoesNaoIntegradasView, ISolicitacoesNaoIntegradasController }
    from "../../contracts/Solicitacao/SolicitacoesNaoIntegradasContract";

import axios from "../../plugins/axios"
import { Storage } from "../../configuration/storage"
export class SolicitacoesNaoIntegradasController implements ISolicitacoesNaoIntegradasController {


    _view: ISolicitacoesNaoIntegradasView

    constructor(view: ISolicitacoesNaoIntegradasView) {
        this._view = view
    }

    importItens(): void {
        this._view.loading(true)

        axios.post('Solicitacao/SolicitacoesNaoIntegradas/' + Storage.get("User") + '/Integracao').then((response: any) => {
            this._view.loading(false)
            if (response.data.sucesso) {
                this._view.showMsg(response.data.message)
                this.carregarItens()
            } else {
                this._view.showMsg(response.data.message)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

    carregarItens(): void {
        this._view.loading(true)

        axios.get('Solicitacao/SolicitacoesNaoIntegradas/' + Storage.get("User")).then((response: any) => {
            this._view.loading(false)
            this._view.showItens(response.data)
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

}